<template>
    <div class="create">
        <div style="height: 80px">
            <detail-header>
                <my-nav :scroll="true"></my-nav>
            </detail-header>
        </div>
        <div class="create-detail">
            <div class="img-select">
                <span :style="{color: 'red'}">*</span>
                <label>选择所属分类: </label>
                <el-cascader
                        v-model="value"
                        :options="options"
                        @change="handleChange"></el-cascader>
            </div>
            <div class="upload-img">
                <span :style="{color: 'red'}">*</span>
                <el-upload
                        class="avatar-uploader"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload"
                        action="/api/upload/img"
                >
                    <img v-if="squareUrl" :src="squareUrl" class="picture">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </div>

            <div class="img-info">
                <label>图片简介</label>
                <div>
                    <el-input
                            type="textarea"
                            :rows="2"
                            placeholder="请输入内容"
                            v-model="textarea">
                    </el-input>
                </div>
            </div>
            <el-button
                    class="send"
                    type="primary"
                    size="medium"
                    @click="submit"
            >提交</el-button>
        </div>

    </div>

</template>

<script>
    import DetailHeader from "../components/DetailHeader";
    import MyNav from "../components/MyNav";
    import { megError } from '@/utils/message'
    import { createWork } from '@/service/user-api'

    export default {
        name: 'Publish',
        components: {
            DetailHeader,
            MyNav
        },
        data() {
            return {
                squareUrl: '',
                dialogImageUrl: '',
                dialogVisible: false,
                textarea: '',
                options: [{
                value: 1,
                label: '旅游风光',
                children: [{
                    value: 1,
                    label: '海滩'
                }, {
                    value: 2,
                    label: '鲜花'
                }, {
                value: 3,
                label: '上海',
                }, {
                    value: 4,
                    label: '深圳',
                }, {
                    value: 5,
                    label: '武汉',
                }, {
                    value: 6,
                    label: '山水',
                }]
            }, {
                value: 2,
                label: '生活',
                children: [{
                    value: 7,
                    label: '户外休闲'
                }, {
                    value: 8,
                    label: '生活方式'
                }, {
                    value: 9,
                    label: '生活色彩'
                }, {
                    value: 10,
                    label: '欢聚时刻'
                }, {
                    value: 11,
                    label: '小情绪'
                }, {
                    value: 12,
                    label: '老年生活'
                }]
            }],
                value: ''
            };
        },
        methods: {
            handleAvatarSuccess(res, file) {
                if (res.code === 2000){
                    this.squareUrl = URL.createObjectURL(file.raw);
                    this.dialogImageUrl = res.url
                } else {
                    megError('上传图片失败!')
                }

            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },
            handleChange(value){
              this.value = value
            },
            submit(){
                let data = {
                    user_id: this.$store.getters.consumerInfo.id,
                    url: this.dialogImageUrl,
                    first_category: this.value[0],
                    second_category: this.value[1],
                    describe: this.textarea
                }
                console.log('submit', )
                createWork(data)
                .then((res) => {
                    if (res.code === 2000){
                        console.log('ok')
                    }
                })
            }
        }
    }
</script>

<style lang="scss">
    .create{
        display: flex;
        justify-content: center;
        .create-detail{
            margin-top: 120px;
            width: 100%;
            /*height: 800px;*/
            background: #fff;
            .img-select{
                margin-top: 20px;
            }
            .upload-img{
                margin-top: 40px;
                /*.el-upload--picture-card{*/
                /*    height: 400px;*/
                /*    width: 1200px;*/
                /*    line-height: 400px;*/
                /*};*/

            };
            .el-select{
                width: 200px;
            };
            .img-info{
                display: flex;
                justify-content: center;
                margin-top: 40px;
                width: 100%;
                align-items: center;
                flex-direction: column;
                .el-textarea__inner{
                    margin-top: 20px;
                    height: 100px;
                    width: 1200px;
                }
            };
            .el-button{
                margin-top: 20px;
                margin-bottom: 40px;
            }

        }

    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 1200px;
        height: 400px;
        line-height: 400px;
        text-align: center;
    }
    .picture {
        height: 400px;
        width: 1200px;
        object-fit: contain;
    }


</style>
